<template>
	<div>
		<h3>Users</h3>
		<b-alert variant="success" show>
			Create and modify users
		</b-alert>
		<b-row>
			<b-col md="2">
				<p>Email</p>
				<b-form-input
					type="text"
					v-model="searchForm.email"
					@keyup.enter="fetch"/>
			</b-col>
			<b-col md="2">
				<p>Username</p>
				<b-form-input
					type="text"
					v-model="searchForm.username"
					@keyup.enter="fetch"/>
			</b-col>
			<b-col md="2">
				<p>Company</p>
				<b-form-select
					v-model="searchForm.company"
					@change="fetch"
				>
					<b-form-select-option :value="-1">All</b-form-select-option>
					<b-form-select-option v-for="(company, i) in companies" :key="i" :value="company._id">{{ company.name }}</b-form-select-option>
				</b-form-select>
			</b-col>
			<b-col md="6" class="text-right">
				<b-button variant="success" @click="add">
					<b-icon icon="plus" scale="1.5"></b-icon> Add
				</b-button>
			</b-col>
		</b-row>
		<b-table
			:fields='fields'
			:items='list'
			:busy="loading"
			outlined
			class="mt-3"
			responsive>
			<template #cell(index)="data">
				<p>{{ (searchForm.page - 1) * searchForm.perPage + data.index + 1 }}</p>
			</template>
			<template #cell(email)="data">
				<p>{{ data.value }}</p>
			</template>
			<template #cell(username)="data">
				<p>{{ data.value }}</p>
			</template>
			<template #cell(company)="data">
				<p>{{ data.item.company && data.item.company.name }}</p>
			</template>
			<template #cell(userType)="data">
				<p>{{ data.value }}</p>
			</template>
			<template #cell(edit)="data">
				<b-button variant="success" size="sm" @click="edit(data.index)" class="mr-2">
					<b-icon icon="pencil"></b-icon> Edit
				</b-button>
				<b-button variant="danger" size="sm" @click="deleteById(data.index)" class="mr-2">
					<b-icon icon="trash"></b-icon> Delete
				</b-button>
				<b-button variant="danger" size="sm" @click="resetPassword(data.index)">
					<b-icon icon="lock"></b-icon> Reset Password
				</b-button>
			</template>
		</b-table>
		<b-pagination
			v-model="searchForm.page"
			:total-rows="total"
			:per-page="searchForm.perPage"
			align="right">
		</b-pagination>

		<b-modal 
			v-model="addModalShow"
			title="Add User"
			hide-header-close 
			@ok="doAdd"
			@cancel="addModalShow=false"
		>
			<b-row>
				<b-col md="4">Email</b-col>
				<b-col md="8">
					<b-form-input
						type="text"
						v-model="user.email"
						:state="!$v.user.email.required || !$v.user.email.email ? false : null"/>
					<b-form-invalid-feedback v-if="!$v.user.email.required">
						Email is required.
					</b-form-invalid-feedback>
					<b-form-invalid-feedback v-if="!$v.user.email.email">
						Email is invalid.
					</b-form-invalid-feedback>
				</b-col>
			</b-row>
			<b-row class="mt-4">
				<b-col md="4">Username</b-col>
				<b-col md="8">
					<b-form-input
						type="text"
						v-model="user.username"
						:state="!$v.user.username.required ? false : null"/>
					<b-form-invalid-feedback v-if="!$v.user.username.required">
						Username is required.
					</b-form-invalid-feedback>
				</b-col>
			</b-row>
			<b-row class="mt-4">
				<b-col md="4">userType</b-col>
				<b-col md="8">
					<b-form-select v-model="user.userType">
						<b-form-select-option value="user">User</b-form-select-option>
						<b-form-select-option value="admin">Admin</b-form-select-option>
						<b-form-select-option value="superadmin">Super Admin</b-form-select-option>
					</b-form-select>
				</b-col>
			</b-row>
			<b-row class="mt-4">
				<b-col md="4">Company</b-col>
				<b-col md="8">
					<b-form-select v-model="user.company">
						<b-form-select-option :value="null"></b-form-select-option>
						<b-form-select-option v-for="(company, i) in companies" :key="i" :value="company._id">{{ company.name }}</b-form-select-option>
					</b-form-select>
				</b-col>
			</b-row>
		</b-modal>

		<b-modal 
			v-model="editModalShow"
			title="Edit User"
			hide-header-close 
			@ok="doEdit"
		>
			<b-row>
				<b-col md="4">Email</b-col>
				<b-col md="8">
					<b-form-input
						type="text"
						v-model="user.email"
						:state="!$v.user.email.required || !$v.user.email.email ? false : null"/>
					<b-form-invalid-feedback v-if="!$v.user.email.required">
						Email is required.
					</b-form-invalid-feedback>
					<b-form-invalid-feedback v-if="!$v.user.email.email">
						Email is invalid.
					</b-form-invalid-feedback>
				</b-col>
			</b-row>
			<b-row class="mt-4">
				<b-col md="4">Username</b-col>
				<b-col md="8">
					<b-form-input
						type="text"
						v-model="user.username"
						:state="!$v.user.username.required ? false : null"/>
					<b-form-invalid-feedback v-if="!$v.user.username.required">
						Username is required.
					</b-form-invalid-feedback>
				</b-col>
			</b-row>
			<b-row class="mt-4">
				<b-col md="4">userType</b-col>
				<b-col md="8">
					<b-form-select v-model="user.userType">
						<b-form-select-option value="user">User</b-form-select-option>
						<b-form-select-option value="admin">Admin</b-form-select-option>
						<b-form-select-option value="superadmin">Super Admin</b-form-select-option>
					</b-form-select>
				</b-col>
			</b-row>
			<b-row class="mt-4">
				<b-col md="4">Company</b-col>
				<b-col md="8">
					<b-form-select v-model="user.company">
						<b-form-select-option :value="null"></b-form-select-option>
						<b-form-select-option v-for="(company, i) in companies" :key="i" :value="company._id">{{ company.name }}</b-form-select-option>
					</b-form-select>
				</b-col>
			</b-row>
		</b-modal>

		<b-modal 
			v-model="deleteModalShow"
			title="Delete User"
			hide-header-close 
			@ok="doDelete"
		>
			Are you sure you want to delete?
		</b-modal>

		<b-modal 
			v-model="resetPasswordModalShow"
			title="Reset Password"
			hide-header-close 
			@ok="doResetPassword"
		>
			Are you sure you want to reset password?
		</b-modal>
	</div>
</template>
<script>
	import { required, email } from 'vuelidate/lib/validators'
	import { mapState } from 'vuex'
	export default {
		name: 'UserView',
		mounted() {
			this.fetch()
		},
		data() {
			return {
				fields: [
					{ key: 'index', label: 'No' },
					{ key: 'email', label: 'Email' },
					{ key: 'username', label: 'Username' },
					{ key: 'company', label: 'Company' },
					{ key: 'userType', label: 'User Type' },
					{ key: 'edit', label: '-'}
				],
				loading: false,
				searchForm: {
					page: 1,
					perPage: 10,
					email: '',
					username: '',
					company: -1
				},
				addModalShow: false,
				editModalShow: false,
				deleteModalShow: false,
				resetPasswordModalShow: false,
				user: {
					_id: '',
					email: '',
					username: '',
					userType: 'user',
					company: null
				}
			}
		},
		validations: {
			user: {
				email: { required, email },
				username: { required }
			}
		},
		computed: {
			...mapState({
				list: state => state.user.list,
				total: state => state.user.total,
				companies: state => state.user.companies
			}) 
		},
		methods: {
			fetch() {
				this.loading = true
				this.$store.dispatch('user/fetch', this.searchForm).then(() => {
					this.loading = false
				})
			},
			add() {
				this.user = {
					_id: '',
					email: '',
					username: '',
					userType: 'user',
					company: null
				}
				this.addModalShow = true
			},
			doAdd(evt) {
				evt.preventDefault()

				this.$v.user.$touch()
				if (this.$v.user.$error) return

				this.addModalShow = false
				this.$store.dispatch('user/add', this.user).then(() => {
					this.$toasted.success('Add Success')
					this.fetch()
				})
			}, 
			edit(idx) {
				this.user = {
					_id: this.list[idx]._id,
					email: this.list[idx].email,
					username: this.list[idx].username,
					userType: this.list[idx].userType,
					company: (this.list[idx].company ? this.list[idx].company._id : null)
				}

				this.editModalShow = true
			},
			doEdit(evt) {
				evt.preventDefault()

				this.$v.user.$touch()
				if (this.$v.user.$error) return

				this.editModalShow = false
				this.$store.dispatch('user/edit', this.user).then(() => {
					this.$toasted.success('Edit Success')
					this.fetch()
				})
			},
			deleteById(idx) {
				this.user._id = this.list[idx]._id
				this.deleteModalShow = true
			},
			doDelete() {
				this.$store.dispatch('user/deleteById', this.user._id).then(() => {
					this.$toasted.success('Delete Success')
					this.fetch()
				})
			},
			resetPassword(idx) {
				this.user._id = this.list[idx]._id
				this.resetPasswordModalShow = true
			},
			doResetPassword() {
				this.$store.dispatch('user/resetPassword', this.user._id).then(() => {
					this.$toasted.success('Reset Password Success')
				})
			}
		},
		watch: {
			'searchForm.page': function() {
				this.fetch()
			}
		}
	}
</script>